<template>
	<div style="overflow: hidden;">
		<div class="flex justify-center align-center" style="width: 100%;height: 60px;background-color: #0099FF;color: #F8F8F8;"><span style="font-size: 20px;font-weight: bolder;">后台操作系统</span></div>
		
		<el-row :gutter="10">
		  <el-col :xs="3" :sm="4" :md="4" :lg="4" :xl="4">
			  <div style="background-color: #2B3C58;">
			  	<dd class="flex justify-center align-center flex-column">
			  		<dt style="font-size: 20px;padding: 20px;color: #FFFFFF;">操作</dt>
			  		
						<router-link v-for="(item,index) in gl" :key="index" :to="{name:item.name}"><dl @click="click(index)" :class="[isActive == index ? 'isActive':' ']" style=" padding: 10px;margin: 20px;color: #FFFFFF;">{{item.content}}</dl></router-link>
						
			  	</dd>
			  </div>
		  </el-col>
		  <el-col :xs="21" :sm="20" :md="20" :lg="20" :xl="20">
			  <div>
				<keep-alive>
					<router-view></router-view>
				</keep-alive>
			  </div>
		  </el-col>
		  
		</el-row>
		
		
		
	</div>
</template>

<script>
	
	export default {
		data() {
			return {
				gl:[
					{
						name:'Messages',
						content:"用户留言"
					},
					{
						name:'Editor',
						content:"文章编辑"
					},
					{
						name:'Wz',
						content:"文章管理"
					}
				],
				isActive: 0
			}
		},
		mounted() {
			this.$store.commit("show",this.$route.fullPath)
			if(this.$route.fullPath === "/management/messages"){
				this.isActive = 0
			}else if(this.$route.fullPath === "/management/editor"){
				this.isActive = 1
			}else{
				this.isActive = 2
			}
		},
		methods: {
			click(index) {
				
				switch (index){
					case 0:
						// console.log(this)
						this.isActive = index
						break;
					case 1:
						this.isActive = index
						break;
					case 2:
						this.isActive = index
						break;
					default:
						break;
				}
			}
		},
		
	}
</script>

<style>
	.isActive{
		background-color: #4D5C73;
	}
</style>
